@function calculateElementSize($element-width, $border-width) {
  @return ($element-width - 2*$border-width);
}

@function calculateCalendarWidth($element-width) {
  @return  $element-width * 7;
}

@function calculateDayShift($element-width, $border-width) {
  @return $element-width - $border-width;
}
